import React, { useState } from "react";
import Sidebar from "../../components/Sidebar";
import Navbar from "../../components/Navbar";
import AchieveHeroSection from "./AchieveHeroSection";
import HowSection from "../../components/HowComponents/HowSection";
import ConnectSection from "../../components/HowComponents/ConnectSection";
import CompleteSection from "../../components/HowComponents/CompleteSection";
import HowFaqSection from "../../components/HowComponents/HowFaqSection";

const Achieve = () => {
  const [isOpen, setIsOpen] = useState(false);

  const toggle = () => {
    setIsOpen(!isOpen);
  };

  return (
    <>
      <Sidebar isOpen={isOpen} toggle={toggle} />
      <Navbar toggle={toggle} />
      {/* <InfoSection {...achieveObjOne} /> */}
      <AchieveHeroSection />
      <HowSection />
      {/* <DesignSection /> */}
      <ConnectSection />
      <CompleteSection />
      <HowFaqSection />
    </>
  );
};

export default Achieve;
