import React from "react";

import img from "./how.jpg";

const HowSection = () => {
  return (
    <div style={{ backgroundColor: "#010606", color: "#fff" }}>
      <div class="container col-xxl-8 px-4 py-5">
        <div class="row flex-lg-row-reverse align-items-center g-5 py-5">
          <div class="col-10 col-sm-8 col-lg-6">
            <img
              src={img}
              class="d-block mx-lg-auto img-fluid"
              alt=" "
              width="700"
              height="500"
              loading="lazy"
            />
          </div>
          <div class="col-lg-6">
            <h1 class="display-5 fw-bold lh-1 mb-3">How it works</h1>
            <p class="lead">
              Innobee strives to create great content and services, with its
              community of professional bees. If you are interested in talking
              to us, feel free to leave your details on the form and one of our
              experts will give you a call.
            </p>
            <div class="d-grid gap-2 d-md-flex justify-content-md-start">
              <button
                type="button"
                to="/Hive"
                class="btn btn-primary btn-lg px-4 me-md-2"
                style={{
                  backgroundColor: "#c68a2c",
                  color: "#fff",
                  borderColor: " #c68a2c",
                }}
              >
                I'm interested!
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default HowSection;
