import React from "react";
import { Link } from "react-router-dom";
import legacyLogo from "../../images/legacy.svg";
import iressLogo from "../../images/iress.svg";
import cloverLogo from "../../images/clover.svg";
import manhattanLogo from "../../images/manhattan.svg";
import supercLogo from "../../images/superc.svg";
import tropikaLogo from "../../images/tropika.svg";
import { FaMapMarker, FaPhone, FaEnvelope } from "react-icons/fa";
import { Button } from "../../components/ButtonElements";

const AboutComponent = () => {
  return (
    <div className="container col-xxl-8 px-4 py-5">
      <div className="breadcrumbDiv">
        <nav aria-label="breadcrumb">
          <ol className="breadcrumb" style={{ backgroundColor: "#fff" }}>
            <li className="breadcrumb-item">
              <Link to="/" style={{ textDecoration: "none" }}>
                About
              </Link>
            </li>
            <li className="breadcrumb-item active" aria-current="page">
              About Us
            </li>
          </ol>
        </nav>
      </div>
      <div className="px-4 py-5 my-5 text-center">
        <img
          className="d-block mx-auto mb-4"
          src="/docs/5.1/assets/brand/bootstrap-logo.svg"
          alt=""
          width="72"
          height="57"
        />
        <h1 className="display-5 fw-bold">Our story</h1>
        <div className="col-lg-6 mx-auto">
          <p className="lead mb-4">
            We have over 15 years of experience within our 100% consultancy
            network that binds our connected colony of skills together.
          </p>
        </div>
      </div>

      <div className="container col-xxl-8 px-4 py-5">
        <div className="row flex-lg-row-reverse align-items-center g-5 py-5">
          <div className="col-10 col-sm-8 col-lg-6">
            <img
              src={legacyLogo}
              className="d-block mx-lg-auto img-fluid"
              alt=" "
              width="700"
              height="500"
              loading="lazy"
            />
          </div>
          <div className="col-lg-6">
            <h3 className="display-5 fw-bold lh-1 mb-3">
              Brands We have worked with
            </h3>
            <p className="lead">
              We have worked with various companies and brands all across South
              Africa, serving them with the best content creation, design and
              marketing services. We pride ourselves with the work we have been
              able to do and continue to do.
            </p>
          </div>
        </div>
      </div>

      <div className="px-4 py-5 my-5 text-center">
        <h1 className="display-5 fw-bold">Our clients</h1>

        <section className="logo-list">
          <div className="container">
            <div className="row">
              <div className="col-lg-2 col-md-4 col-6">
                <Link to="#">
                  <img
                    src={legacyLogo}
                    className="img-fluid"
                    alt="Legacy logo"
                  />
                </Link>
              </div>
              <div className="col-lg-2 col-md-4 col-6">
                <Link to="#">
                  <img src={iressLogo} className="img-fluid" alt="Iress logo" />
                </Link>
              </div>
              <div className="col-lg-2 col-md-4 col-6">
                <Link to="#">
                  <img
                    src={cloverLogo}
                    className="img-fluid"
                    alt="Clover logo"
                  />
                </Link>
              </div>
              <div className="col-lg-2 col-md-4 col-6">
                <Link to="#">
                  <img
                    src={supercLogo}
                    className="img-fluid"
                    alt="Super C logo"
                  />
                </Link>
              </div>
              <div className="col-lg-2 col-md-4 col-6">
                <Link to="#">
                  <img
                    src={tropikaLogo}
                    className="img-fluid"
                    alt="Tropika logo"
                  />
                </Link>
              </div>
              <div className="col-lg-2 col-md-4 col-6">
                <Link to="#">
                  <img
                    src={manhattanLogo}
                    className="img-fluid"
                    alt="Manhattan logo"
                  />
                </Link>
              </div>
            </div>
          </div>
        </section>

        <div className="px-4 py-5 my-5 text-center">
          <h2 class="display-5 fw-bold lh-1 mb-3">
            Privacy Policy for Innobee Content Creators
          </h2>

          <p className="lead mb-4" style={{ textAlign: "justify" }}>
            <p>
              At Innobee Content Creators, accessible from Innobeedigital.com,
              one of our main priorities is the privacy of our visitors. This
              Privacy Policy document contains types of information that is
              collected and recorded by Innobee Content Creators and how we use
              it.
            </p>

            <p>
              If you have additional questions or require more information about
              our Privacy Policy, do not hesitate to contact us.
            </p>

            <p>
              This Privacy Policy applies only to our online activities and is
              valid for visitors to our website with regards to the information
              that they shared and/or collect in Innobee Content Creators. This
              policy is not applicable to any information collected offline or
              via channels other than this website.
            </p>

            <h2>Consent</h2>

            <p>
              By using our website, you hereby consent to our Privacy Policy and
              agree to its terms.
            </p>

            <h2>Information we collect</h2>

            <p>
              The personal information that you are asked to provide, and the
              reasons why you are asked to provide it, will be made clear to you
              at the point we ask you to provide your personal information.
            </p>
            <p>
              If you contact us directly, we may receive additional information
              about you such as your name, email address, phone number, the
              contents of the message and/or attachments you may send us, and
              any other information you may choose to provide.
            </p>
            <p>
              When you register for an Account, we may ask for your contact
              information, including items such as name, company name, address,
              email address, and telephone number.
            </p>

            <h2>How we use your information</h2>

            <p>
              We use the information we collect in various ways, including to:
            </p>

            <ul>
              <li>Provide, operate, and maintain our website</li>
              <li>Improve, personalize, and expand our website</li>
              <li>Understand and analyze how you use our website</li>
              <li>
                Develop new products, services, features, and functionality
              </li>
              <li>
                Communicate with you, either directly or through one of our
                partners, including for customer service, to provide you with
                updates and other information relating to the website, and for
                marketing and promotional purposes
              </li>
              <li>Send you emails</li>
              <li>Find and prevent fraud</li>
            </ul>

            <h2>Log Files</h2>

            <p>
              Innobee Content Creators follows a standard procedure of using log
              files. These files log visitors when they visit websites. All
              hosting companies do this and a part of hosting services'
              analytics. The information collected by log files include internet
              protocol IP addresses, browser type, Internet Service Provider
              ISP, date and time stamp, referring/exit pages, and possibly the
              number of clicks. These are not linked to any information that is
              personally identifiable. The purpose of the information is for
              analyzing trends, administering the site, tracking users' movement
              on the website, and gathering demographic information.
            </p>

            <h2>Cookies and Web Beacons</h2>

            <p>
              Like any other website, Innobee Content Creators uses 'cookies'.
              These cookies are used to store information including visitors'
              preferences, and the pages on the website that the visitor
              accessed or visited. The information is used to optimize the
              users' experience by customizing our web page content based on
              visitors' browser type and/or other information.
            </p>

            <p>For more general information on cookies, please read</p>

            <h2>Google DoubleClick DART Cookie</h2>

            <p>
              Google is one of a third-party vendor on our site. It also uses
              cookies, known as DART cookies, to serve ads to our site visitors
              based upon their visit to www.website.com and other sites on the
              internet. However, visitors may choose to decline the use of DART
              cookies by visiting the Google ad and content network Privacy
              Policy.
            </p>

            <h2>Advertising Partners Privacy Policies</h2>

            <p>
              You may consult this list to find the Privacy Policy for each of
              the advertising partners of Innobee Content Creators.
            </p>

            <p>
              Third-party ad servers or ad networks uses technologies like
              cookies, JavaScript, or Web Beacons that are used in their
              respective advertisements and links that appear on Innobee Content
              Creators, which are sent directly to users' browser. They
              automatically receive your IP address when this occurs. These
              technologies are used to measure the effectiveness of their
              advertising campaigns and/or to personalize the advertising
              content that you see on websites that you visit.
            </p>

            <p>
              Note that Innobee Content Creators has no access to or control
              over these cookies that are used by third-party advertisers.
            </p>

            <h2>Third Party Privacy Policies</h2>

            <p>
              Innobee Content Creators's Privacy Policy does not apply to other
              advertisers or websites. Thus, we are advising you to consult the
              respective Privacy Policies of these third-party ad servers for
              more detailed information. It may include their practices and
              instructions about how to opt-out of certain options.
            </p>

            <p>
              You can choose to disable cookies through your individual browser
              options. To know more detailed information about cookie management
              with specific web browsers, it can be found at the browsers'
              respective websites.
            </p>

            <h2>CCPA Privacy Rights (Do Not Sell My Personal Information)</h2>

            <p>
              Under the CCPA, among other rights, California consumers have the
              right to:
            </p>
            <p>
              Request that a business that collects a consumer's personal data
              disclose the categories and specific pieces of personal data that
              a business has collected about consumers.
            </p>
            <p>
              Request that a business delete any personal data about the
              consumer that a business has collected.
            </p>
            <p>
              Request that a business that sells a consumer's personal data, not
              sell the consumer's personal data.
            </p>
            <p>
              If you make a request, we have one month to respond to you. If you
              would like to exercise any of these rights, please contact us.
            </p>

            <h2>GDPR Data Protection Rights</h2>

            <p>
              We would like to make sure you are fully aware of all of your data
              protection rights. Every user is entitled to the following:
            </p>
            <p>
              The right to access  You have the right to request copies of your
              personal data. We may charge you a small fee for this service.
            </p>
            <p>
              The right to rectification  You have the right to request that we
              correct any information you believe is inaccurate. You also have
              the right to request that we complete the information you believe
              is incomplete.
            </p>
            <p>
              The right to erasure  You have the right to request that we erase
              your personal data, under certain conditions.
            </p>
            <p>
              The right to restrict processing  You have the right to request
              that we restrict the processing of your personal data, under
              certain conditions.
            </p>
            <p>
              The right to object to processing  You have the right to object
              to our processing of your personal data, under certain conditions.
            </p>
            <p>
              The right to data portability  You have the right to request that
              we transfer the data that we have collected to another
              organization, or directly to you, under certain conditions.
            </p>
            <p>
              If you make a request, we have one month to respond to you. If you
              would like to exercise any of these rights, please contact us.
            </p>

            <h2>Children's Information</h2>

            <p>
              Another part of our priority is adding protection for children
              while using the internet. We encourage parents and guardians to
              observe, participate in, and/or monitor and guide their online
              activity.
            </p>

            <p>
              Innobee Content Creators does not knowingly collect any Personal
              Identifiable Information from children under the age of 13. If you
              think that your child provided this kind of information on our
              website, we strongly encourage you to contact us immediately and
              we will do our best efforts to promptly remove such information
              from our records.
            </p>
          </p>
        </div>
      </div>

      {/* CONTACT FORM */}

      <section className="mb-4">
        <h3 className="display-5 fw-bold text-center">Contact us</h3>

        <p className="lead text-center w-responsive mx-auto mb-5">
          Do you have any questions? Please do not hesitate to contact us
          directly. Our team will come back to you within a matter of hours to
          help you.
        </p>

        <div className="row">
          <div className="col-md-9 mb-md-0 mb-5">
            <form
              id="contact-form"
              name="contact-form"
              action="mail.php"
              method="POST"
            >
              <div className="row">
                <div className="col-md-6">
                  <div className="md-form mb-0">
                    <input
                      type="text"
                      id="name"
                      name="name"
                      className="form-control"
                    />
                    <label for="name" className="lead">
                      Your name
                    </label>
                  </div>
                </div>

                <div className="col-md-6">
                  <div className="md-form mb-0">
                    <input
                      type="text"
                      id="email"
                      name="email"
                      className="form-control"
                    />
                    <label for="email" className="lead">
                      Your email
                    </label>
                  </div>
                </div>
              </div>

              <div className="row">
                <div className="col-md-12">
                  <div className="md-form mb-0">
                    <input
                      type="text"
                      id="subject"
                      name="subject"
                      className="form-control"
                    />
                    <label for="subject" className="lead">
                      Subject
                    </label>
                  </div>
                </div>
              </div>

              <div className="row">
                <div className="col-md-12">
                  <div className="md-form">
                    <textarea
                      type="text"
                      id="message"
                      name="message"
                      rows="2"
                      className="form-control md-textarea"
                    ></textarea>
                    <label for="message" className="lead">
                      Your message
                    </label>
                  </div>
                </div>
              </div>
            </form>

            <div className="text-center text-md-left ">
              <Button onclick="document.getElementById('contact-form').submit();">
                Send
              </Button>
            </div>
            <div className="status"></div>
          </div>

          <div className="col-md-3 text-center">
            <ul className="list-unstyled mb-0" style={{ textAlign: "left" }}>
              <li>
                <FaMapMarker className="fa-2x"></FaMapMarker>
                <p className="lead">San Francisco, CA 94126, USA</p>
              </li>

              <li>
                <FaPhone className="mt-4 fa-2x" />
                <p className="lead">+ 01 234 567 89</p>
              </li>

              <li>
                <FaEnvelope className="mt-4 fa-2x" />
                <p className="lead">contact@Innobeedigital.com</p>
              </li>
            </ul>
          </div>
        </div>
      </section>
    </div>
  );
};

export default AboutComponent;
