import React, { useEffect, useState } from "react";
import { FaBars } from "react-icons/fa";
import { IconContext } from "react-icons/lib";
import { animateScroll as scroll } from "react-scroll";
import {
  Nav,
  NavbarContainer,
  NavLogo,
  MobileIcon,
  NavMenu,
  NavItem,
  NavLinks,
  NavBtn,
  NavBtnLink,
} from "./NavbarElements";
import logo from "./logo.png";

const Navbar = ({ toggle }) => {
  const [scrollNav, setScrollNav] = useState(false);

  const changeNav = () => {
    if (window.scrollY >= 80) {
      setScrollNav(true);
    } else {
      setScrollNav(false);
    }
  };

  useEffect(() => {
    window.addEventListener("scroll", changeNav);
  }, []);

  const toggleHome = () => {
    scroll.scrollToTop();
  };
  const toggleContent = () => {
    scroll.scrollToTop();
  };
  const toggleAchieve = () => {
    scroll.scrollToTop();
  };
  const toggleBlog = () => {
    scroll.scrollToTop();
  };
  const toggleAbout = () => {
    scroll.scrollToTop();
  };

  return (
    <>
      <IconContext.Provider value={{ color: "#fff" }}>
        <Nav scrollNav={scrollNav}>
          <NavbarContainer>
            <NavLogo to="/" onClick={toggleHome}>
              <img
                src={logo}
                alt=" "
                width="150"
                height="70"
                className="d-inline-block align-top"
              />
            </NavLogo>
            <MobileIcon onClick={toggle}>
              <FaBars />
            </MobileIcon>
            <NavMenu>
              <NavItem>
                <NavLinks
                  to="/content"
                  activeStyle={{ color: "#C68A2C" }}
                  onClick={toggleContent}
                >
                  Categories
                </NavLinks>
              </NavItem>
              <NavItem>
                <NavLinks
                  to="/achieve"
                  activeStyle={{ color: "#C68A2C" }}
                  onClick={toggleAchieve}
                >
                  How it works
                </NavLinks>
              </NavItem>
              <NavItem>
                <NavLinks
                  to="/Hive"
                  activeStyle={{ color: "#C68A2C" }}
                  onClick={toggleBlog}
                >
                  Innobee Hive
                </NavLinks>
              </NavItem>
            </NavMenu>
            <NavBtn>
              <NavBtnLink to="/signin">Sign In</NavBtnLink>
            </NavBtn>
          </NavbarContainer>
        </Nav>
      </IconContext.Provider>
    </>
  );
};

export default Navbar;
