import React from "react";
import { Link } from "react-router-dom";

const HowFaqSection = () => {
  return (
    <div id="FaqBody" style={{ color: "grey" }}>
      <div
        className="faq_area section_padding_130"
        id="faq"
        style={{ backgroundColor: "#fff" }}
      >
        <div className="container">
          <div className="row justify-content-center">
            <div className="col-12 col-sm-8 col-lg-6">
              {/* <!-- Section Heading--> */}
              <div
                className="section_heading text-center wow fadeInUp"
                data-wow-delay="0.2s"
                style={{
                  visibility: "visible",
                  animationDelay: "0.2s",
                  animationName: "fadeInUp",
                }}
              >
                <h3>
                  <span>Frequently </span> Asked Questions
                </h3>
                <p>
                  Our Innobee support team is here to help you achieve the best
                  content creation possible. Check out our FAQs, send us an
                  email, or give us a call.
                </p>
                <div className="line"></div>
              </div>
            </div>
          </div>
          <div className="row justify-content-center">
            {/* <!-- FAQ Area--> */}
            <div className="col-12 col-sm-10 col-lg-8">
              <div class="accordion faq-accordian" id="faqAccordion">
                <div
                  class="card border-0 wow fadeInUp"
                  data-wow-delay="0.2s"
                  style={{
                    visibility: "visible",
                    animationDelay: "0.2s",
                    animationName: "fadeInUp",
                  }}
                >
                  <div class="card-header" id="headingOne">
                    <h6
                      class="mb-0 collapsed"
                      data-toggle="collapse"
                      data-target="#collapseOne"
                      aria-expanded="true"
                      aria-controls="collapseOne"
                      style={{ color: "#c68a2c" }}
                    >
                      Free Consultation on services
                      <span class="lni-chevron-up"></span>
                    </h6>
                  </div>
                  <div
                    class="collapse"
                    id="collapseOne"
                    aria-labelledby="headingOne"
                    data-parent="#faqAccordion"
                  >
                    <div class="card-body">
                      <p class="lead">
                        Innobee Consultants can answer your questions, and
                        assist you in the briefing process.
                      </p>
                      <p>
                        Help you find a category suitable to your business
                        needs.
                      </p>
                    </div>
                  </div>
                </div>
                <div
                  class="card border-0 wow fadeInUp"
                  data-wow-delay="0.3s"
                  style={{
                    visibility: "visible",
                    animationDelay: "0.3s",
                    animationName: "fadeInUp",
                  }}
                >
                  <div class="card-header" id="headingTwo">
                    <h6
                      class="mb-0 collapsed"
                      data-toggle="collapse"
                      data-target="#collapseTwo"
                      aria-expanded="true"
                      aria-controls="collapseTwo"
                      style={{ color: "#c68a2c" }}
                    >
                      What is the time period per project?
                      <span class="lni-chevron-up"></span>
                    </h6>
                  </div>
                  <div
                    class="collapse"
                    id="collapseTwo"
                    aria-labelledby="headingTwo"
                    data-parent="#faqAccordion"
                  >
                    <div class="card-body">
                      <p>
                        The length of the project will depend on the type of
                        project it is.
                      </p>
                      <p>
                        If it is a small project, the time period will be
                        shorter and if it is a bigger or more scaled up project,
                        the time period will be longer.
                      </p>
                    </div>
                  </div>
                </div>
                <div
                  class="card border-0 wow fadeInUp"
                  data-wow-delay="0.4s"
                  style={{
                    visibility: "visible",
                    animationDelay: "0.4s",
                    animationName: "fadeInUp",
                  }}
                >
                  <div class="card-header" id="headingThree">
                    <h6
                      class="mb-0 collapsed"
                      data-toggle="collapse"
                      data-target="#collapseThree"
                      aria-expanded="true"
                      aria-controls="collapseThree"
                      style={{ color: "#c68a2c" }}
                    >
                      Does time affect the pricing?
                      <span class="lni-chevron-up"></span>
                    </h6>
                  </div>
                  <div
                    class="collapse"
                    id="collapseThree"
                    aria-labelledby="headingThree"
                    data-parent="#faqAccordion"
                  >
                    <div class="card-body">
                      <p>
                        We have pricing for each catalog, and you can choose
                        what works for you, or the range you see that best fits
                        your requirements.
                      </p>
                    </div>
                  </div>
                </div>
              </div>
              {/* <!-- Support Button--> */}
              <div
                class="support-button text-center d-flex align-items-center justify-content-center mt-4 wow fadeInUp"
                data-wow-delay="0.5s"
                style={{
                  visibility: "visible",
                  animationDelay: "0.5s",
                  animationName: "fadeInUp",
                }}
              >
                <i class="lni-emoji-sad"></i>
                <p class="mb-0 px-2">Can't find your answers?</p>
                <Link to="/"> Contact us</Link>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default HowFaqSection;
