// import React, {useState} from 'react';
import React from 'react';
import Video from '../../videos/video.mp4';
import { HeroContainer } from './HeroElements';
import { VideoBg } from './HeroElements';
import { HeroBg } from './HeroElements';
import { 
    HeroContent, 
    HeroP, 
    HeroH1 
} from './HeroElements';


const HeroSection = () => {
    

    return (
        <HeroContainer>
            <HeroBg>
                <VideoBg autoPlay loop muted src={Video} type='video/mp4' />
            </HeroBg>
            <HeroContent>
                <HeroH1>Results-Driven Content Creators</HeroH1>
            <HeroP>Innobee is a dynamic entrepreneurial matrix network, which makes content creation simple, helping your company achieve its goals.</HeroP>
            </HeroContent>
        </HeroContainer>
    )
}

export default HeroSection
