import React from "react";
import sideImage from "./svg-3.svg";
import sideImage2 from "./svg-4.svg";
import { Link } from "react-router-dom";
import { FaBorderNone } from "react-icons/fa";

const OptionSection = () => {
  return (
    <div
      className="px-4 py-5 my-5 text-center"
      style={{ backgroundColor: "#010606", color: "#fff" }}
    >
      <h1 className="display-5 fw-bold">How to get a design</h1>
      <div className="col-lg-6 mx-auto">
        <p className="lead mb-4">
          Innobees tried-and-true creative process makes content creation magic.
          With our national community that loves to collaborate, well turn your
          great ideas into custom content you cant get anywhere else.
        </p>
      </div>

      <div className="container col-xxl-8 px-4 py-5">
        <div className="row flex-lg-row-reverse align-items-center g-5 py-5">
          <div className="col-10 col-sm-8 col-lg-6">
            <img
              src={sideImage}
              className="d-block mx-lg-auto img-fluid"
              alt=" "
              width="400"
              height="300"
              loading="lazy"
            />
            <p class="lead">
              Innobee drives business results with services that help your
              company achieve its goals including, Lead Generation, Search
              Engine optimisation, Influencer programs and public relations
            </p>
          </div>
          <div className="col-10 col-sm-8 col-lg-6">
            <img
              src={sideImage2}
              className="d-block mx-lg-auto img-fluid"
              alt=" "
              width="400"
              height="300"
              loading="lazy"
            />
            <p class="lead">
              Innobee strives to share skills and quality work from its hive of
              professional content creators with many organisations
            </p>
          </div>
        </div>
      </div>

      <div className="d-grid gap-2 d-sm-flex justify-content-sm-center">
        <button
          type="button"
          className="btn btn-primary btn-lg px-4 gap-3"
          style={{
            backgroundColor: "#c68a2c",
            color: "#fff",
            borderColor: " #c68a2c",
          }}
        >
          <Link className="styleLink" to="/Hive">
            View Team
          </Link>
        </button>
        <button type="button" className="btn btn-outline-secondary btn-lg px-4">
          <Link className="styledLink" to="/achieve">
            How it works
          </Link>
        </button>
      </div>
    </div>
  );
};

export default OptionSection;
