import React from "react";
import {
  HeroContainer,
  HeroBg,
  HeroContent,
  HeroP,
  HeroH1,
  ImgBg,
} from "../../components/HeroSection/HeroElements";
import Img from "../About/bgimage.jpg";

const AboutHeroSection = () => {
  return (
    <>
      <HeroContainer>
        <HeroBg>
          <ImgBg src={Img} />
        </HeroBg>
        <HeroContent>
          <HeroH1>Get to know Innobee more</HeroH1>
          <HeroP>
            We have over 15 years of experience within our 100% consultancy
            network that binds our connected colony of skills together.
          </HeroP>
        </HeroContent>
      </HeroContainer>
    </>
  );
};

export default AboutHeroSection;
