import React from "react";
import img from "../Blog/circle.png";
import { Link } from "react-router-dom";

const Careers = () => {
  return (
    <div style={{ zIndex: "9999" }}>
      <div class="container col-xxl-8 px-4 py-5">
        <div className="breadcrumbDiv">
          <nav aria-label="breadcrumb">
            <ol class="breadcrumb" style={{ backgroundColor: "#fff" }}>
              <li class="breadcrumb-item">
                <Link to="/" style={{ textDecoration: "none" }}>
                  Home
                </Link>
              </li>
              <li class="breadcrumb-item active" aria-current="page">
                Careers
              </li>
            </ol>
          </nav>
        </div>

        <div class="row flex-lg-row-reverse align-items-center g-5 py-5">
          <div class="col-10 col-sm-8 col-lg-6">
            <img
              src={img}
              class="d-block mx-lg-auto img-fluid"
              alt=" "
              width="500"
              height="300"
              loading="lazy"
            />
          </div>
          <div class="col-lg-6">
            <div
              class="d-flex justify-content-center align-items-center"
              id="404page"
            >
              <h1 class="mr-3 pr-3 align-top border-right inline-block align-content-center">
                404
              </h1>
              <div class="inline-block align-middle">
                <h2 class="font-weight-normal lead" id="desc">
                  The page you requested was not found.
                  <br />
                  Click the breadcrumb to navigate to home
                </h2>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Careers;
