import React from "react";
import {
  HeroContainer,
  HeroBg,
  HeroContent,
  HeroP,
  HeroH1,
  ImgBg,
} from "../../components/HeroSection/HeroElements";
import Img from "../Achieve/bg.jpg";

const AchieveHeroSection = () => {
  return (
    <>
      <HeroContainer>
        <HeroBg>
          <ImgBg src={Img} />
        </HeroBg>
        <HeroContent>
          <HeroH1>Buzzworthy-Content the Bees have generated</HeroH1>
          <HeroP>
            Innobee has carried out various successful projects with big
            corporations.
          </HeroP>
        </HeroContent>
      </HeroContainer>
    </>
  );
};

export default AchieveHeroSection;
