import React from "react";
import logo from "../components/Signin/logo.png";
import {
  Container,
  FormWrap,
  Icon,
  FormContent,
  Form,
  FormH1,
  FormLabel,
  FormInput,
  FormButton,
  Text,
} from "../components/Signin/SigninElements";
import { Link } from "react-router-dom";

const register = () => {
  return (
    <div>
      <Container>
        <FormWrap>
          <div
            className="breadcrumbDiv"
            style={{ marginTop: "5px", marginLeft: "5px" }}
          >
            <nav aria-label="breadcrumb">
              <ol className="breadcrumb" style={{ backgroundColor: "#010606" }}>
                <li className="breadcrumb-item">
                  <Link to="/" style={{ textDecoration: "none" }}>
                    Home
                  </Link>
                </li>
                <li className="breadcrumb-item active" aria-current="page">
                  Register
                </li>
              </ol>
            </nav>
          </div>
          <FormContent>
            <Form action="/auth/register" method="POST">
              <Icon to="/">
                <img src={logo} alt=" " width="150" height="70" />
              </Icon>
              <FormH1 style={{ marginBottom: "20px", fontSize: "14px" }}>
                Sign in to your account
              </FormH1>
              <FormLabel htmlFor="for">Name</FormLabel>
              <FormInput
                type="text"
                class="form-control"
                id="name"
                name="name"
                required
              />
              <FormLabel htmlFor="for">Email</FormLabel>
              <FormInput
                type="email"
                class="form-control"
                id="email"
                name="email"
                required
              />
              <FormLabel htmlFor="for">Password</FormLabel>
              <FormInput
                type="password"
                class="form-control"
                id="password"
                name="password"
                required
              />

              <FormLabel htmlFor="for">Password Confirm</FormLabel>
              <FormInput
                type="password"
                class="form-control"
                id="passwordConfirm"
                name="passwordConfirm"
                required
              />

              <FormButton type="submit">Continue</FormButton>
              <Text>Forgot password</Text>
              <p className="text-muted text-center mt-3 mb-0">
                Already have an account?
                <Link to="/signin" className="text-primary ml-1">
                  sign in
                </Link>
              </p>
            </Form>
          </FormContent>
        </FormWrap>
      </Container>
    </div>
  );
};

export default register;
