import React from "react";
import circle from "../../pages/Blog/circle.png";

const ConnectSection = () => {
  return (
    <div className="px-4 py-5 my-5 text-center">
      <img
        className="d-block mx-auto mb-4"
        src={circle}
        alt=""
        width="72"
        height="57"
      />
      <h1 className="display-5 fw-bold">Work with the Best Content Creators</h1>
      <div className="col-lg-6 mx-auto">
        <p className="lead mb-4">
          We offer amazing ways to work with our community of professionals.
          Innobee delivers the best quality content creation.
        </p>
      </div>
    </div>
  );
};

export default ConnectSection;
