import React from "react";
import {
  HeroContainer,
  HeroBg,
  HeroContent,
  HeroP,
  HeroH1,
  ImgBg,
} from "../../components/HeroSection/HeroElements";
import Img from "../Content/bgimg.jpg";

const ContentHeroSection = () => {
  return (
    <>
      <HeroContainer style={{ maxHeight: "650px" }}>
        <HeroBg>
          <ImgBg src={Img} />
        </HeroBg>
        <HeroContent>
          <HeroH1>Innobee Content Creation Services</HeroH1>
          <HeroP style={{ maxWidth: "700px" }}>
            Innobee specialises in creating online conversations that fly,
            developing engaging content that inspires emotional connections.
            Inoobee delivers buzz-worthy results and works zealously to share in
            some tenacious values of your business to ensure your brand
            blossoms.
          </HeroP>
        </HeroContent>
      </HeroContainer>
    </>
  );
};

export default ContentHeroSection;
