import React from "react";
import { Link } from "react-router-dom";
import { FaPlus } from "react-icons/fa";

export const CompleteSection = () => {
  return (
    <div className="container col-xxl-8 px-4 py-5">
      <div className="row flex-lg-row-reverse align-items-center g-5 py-5">
        <div className="col-10 col-sm-8 col-lg-6">
          <img
            src="bootstrap-themes.png"
            className="d-block mx-lg-auto img-fluid"
            alt="Bootstrap Themes"
            width="700"
            height="500"
            loading="lazy"
          />
        </div>
        <div className="col-lg-6">
          <h1 className="display-5 fw-bold lh-1 mb-3">
            Finalize your design and continue working together
          </h1>
          <p className="lead">
            Once youre happy with your design, you can begin discussing your
            next project together.
            <ul className="style-List">
              <li>Securely release payment upon completion</li>
              <li>Production ready files for print and digital</li>
              <li>Copyright and ownership is all yours</li>
            </ul>
          </p>
          <div className="col-lg-6">
            <div className="heading-section mb-5 mt-5 mt-lg-0">
              <h2 className="mb-3">What do you need designed?</h2>
              <p>
                You can start the process by creating a simple brief to help our
                design team understand your design needs.
                <ul className="style-List">
                  <li>Quick and easy process</li>
                  <li>Your style and spec requirements are noted</li>
                  <li>All project sizes welcome</li>
                </ul>
              </p>
            </div>
            <div
              id="accordion"
              className="myaccordion w-100"
              aria-multiselectable="true"
            >
              <div className="card">
                <div className="card-header p-0" id="headingOne">
                  <h2
                    className="mb-0"
                    style={{
                      backgroundColor: "#fff",
                    }}
                  >
                    <button
                      to="#collapseOne"
                      className="d-flex py-3 px-4 align-items-center justify-content-between btn btn-link"
                      data-parent="#accordion"
                      data-toggle="collapse"
                      aria-expanded="true"
                      aria-controls="collapseOne"
                      style={{
                        textDecoration: "none",
                        color: "grey",
                        backgroundColor: "#fff",
                      }}
                    >
                      <p className="mb-0">What files do I need? &emsp;</p>
                      <FaPlus className="fa" aria-hidden="true" />
                    </button>
                  </h2>
                </div>
                <div
                  className="collapse show"
                  id="collapseOne"
                  role="tabpanel"
                  aria-labelledby="headingOne"
                  // style=""
                >
                  <div className="card-body py-3 px-0">
                    If you need files for digital use then you need JPG, GIF or
                    PNG files in RGB color mode. If youre getting designs
                    printed then youll need PDF, Illustrator, Photoshop or
                    InDesign files in CMYK color mode.
                  </div>
                </div>
              </div>
              <div className="card">
                <div className="card-header p-0" id="headingTwo" role="tab">
                  <h2
                    className="mb-0"
                    style={{
                      backgroundColor: "#fff",
                    }}
                  >
                    <button
                      to="#collapseTwo"
                      className="d-flex py-3 px-4 align-items-center justify-content-between btn btn-link collapsed"
                      data-parent="#accordion"
                      data-toggle="collapse"
                      aria-expanded="false"
                      aria-controls="collapseTwo"
                      style={{
                        textDecoration: "none",
                        color: "grey",
                        backgroundColor: "#fff",
                      }}
                    >
                      <p className="mb-0">Where are my design files stored?</p>
                      <FaPlus className="fa" aria-hidden="true" />
                    </button>
                  </h2>
                </div>
                <div
                  className="collapse"
                  id="collapseTwo"
                  role="tabpanel"
                  aria-labelledby="headingTwo"
                  // style=""
                >
                  <div className="card-body py-3 px-0">
                    All your design files are conveniently and securely stored
                    in Innobee's database system available on request.
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default CompleteSection;
